*:focus {
  outline: none;
}

body {
  text-align: left;
}

h1, .h1 {
  font-family: "Open Sans Light", "Segoe UI Light", "Segoe UI", sans-serif;
  color: #377796;
  margin: 0;
  letter-spacing: -1px;
  line-height: 1;
}

h2, .h2 {
  color: #377796;
  font-family: "Open Sans Light", "Segoe UI Light", "Segoe UI", sans-serif;
  margin-top: 10px;
  margin-bottom: 0;
  line-height: 1;
  letter-spacing: -1px;
}

h3, .h3 {
  font-family: "Open Sans Extrabold", "Segoe Black", sans-serif;
  text-transform: uppercase;
}

input,
select {
  font-size: 1em;
}

textarea {
  font-family: "Consolas", "Lucida Grande", monospace;
  font-size: 1.2em;
}

.table {
  caption-side: top;
}
.table td {
  touch-action: manipulation;
  vertical-align: middle;
}
.table th {
  background-color: #f7f7f7;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: normal;
  border-bottom: 1px solid #ddd;
}
.table th a {
  color: #666;
}
.table caption {
  font-family: "Open Sans Bold", sans-serif;
  background-color: #f7f7f7;
  font-weight: normal;
  text-align: center;
}

@media only screen and (min-width: 768px) {
  .table th.position-sticky {
    top: 87px;
  }
}
select#fieldsSelect,
textarea#sqlquery {
  margin: 0;
  height: 20rem;
}

select.form-control {
  padding: 4px;
}

.nav-pills .nav-link {
  margin: 0;
  padding: 5px 15px;
  font-weight: normal;
  background-color: #f6f6f6;
  color: #333;
  text-transform: uppercase;
  font-family: "Open Sans Extrabold", "Segoe Black", sans-serif;
  border-radius: 0;
}
.nav-pills .nav-link:hover {
  background-color: #f7f7f7;
  text-decoration: underline;
}
.nav-pills .nav-link img {
  margin-right: 0.5em;
  vertical-align: -3px;
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background: #ccc;
  color: #333;
}

.nav-tabs {
  font-weight: bold;
}
.nav-tabs .nav-link {
  font-family: "Open Sans Bold", sans-serif;
  background-color: #f2f2f2;
  color: #555;
  border-color: #fff #fff #ddd;
  margin-right: 0.4em;
  padding: 7px 10px;
}
.nav-tabs .nav-link:hover, .nav-tabs .nav-link:focus {
  background-color: #e5e5e5;
}
.nav-tabs .nav-link.active:hover, .nav-tabs .nav-link.active:focus,
.nav-tabs .nav-item.show .nav-link:hover,
.nav-tabs .nav-item.show .nav-link:focus {
  background-color: #fff;
}

.navbar {
  background-color: #f7f7f7;
  padding-left: 20px;
  padding-right: 20px;
}

.navbar-nav .icon {
  margin-right: 0.5em;
  vertical-align: -3px;
}

.navbar-nav .nav-item {
  padding: 8px 10px;
}
.navbar-nav .nav-item.active {
  background: #fff;
}

.navbar-nav .nav-link {
  text-transform: uppercase;
  font-family: "Open Sans Extrabold", "Segoe Black", sans-serif;
  font-weight: normal;
}

.card {
  margin-top: 1rem;
}

.card-header {
  font-family: "Open Sans Light", "Segoe UI Light", "Segoe UI", sans-serif;
  font-size: 1rem;
  border-bottom: 0;
}

.card-img-container {
  height: 16vh;
  overflow: hidden;
}
.card-img-container img {
  object-fit: cover;
}

.card-footer {
  border-top: 0;
}

#maincontainer .card {
  background: none;
  border: none;
  margin: 0;
}
#maincontainer .card-header {
  background: none;
  color: #377796;
  font-size: 2.8em;
  font-family: "Open Sans Light", "Segoe UI Light", "Segoe UI", sans-serif;
  padding: 0;
}
#maincontainer .card-body ul {
  padding-left: 0;
}

.breadcrumb-navbar {
  padding: 0.89rem 2rem;
  margin-bottom: 0;
  background-color: #377796;
}
.breadcrumb-navbar .breadcrumb-item + .breadcrumb-comment {
  padding-left: 0.5rem;
}
.breadcrumb-navbar .breadcrumb-item .icon {
  margin: 0;
}

.breadcrumb-navbar {
  color: #fff;
}
.breadcrumb-navbar a {
  color: inherit;
}
.breadcrumb-navbar a:hover {
  text-decoration: none;
}
.breadcrumb-navbar .breadcrumb-item + .breadcrumb-item::before, .breadcrumb-navbar .breadcrumb-item::before {
  width: 10px;
  color: #eee;
  /* The arrow "U+0203A" (SINGLE RIGHT-POINTING ANGLE QUOTATION MARK) */
  content: "";
}
.breadcrumb-navbar .breadcrumb-item .icon {
  display: none;
}
.breadcrumb-navbar .breadcrumb-comment {
  font-style: italic;
}

.alert {
  text-align: left;
}
.alert a {
  text-decoration: underline;
}
.alert h1, .alert .h1 {
  margin-bottom: 0.2em;
}

.alert-primary {
  color: #fff;
  background-color: #3a7ead;
  background-image: none;
}
.alert-primary a {
  color: #fff;
}
.alert-primary h1, .alert-primary .h1 {
  color: #eee;
}

.alert-success {
  color: #fff;
  background-color: #01a31c;
  background-image: none;
}
.alert-success a {
  color: #fff;
}
.alert-success h1, .alert-success .h1 {
  color: #eee;
}

.alert-danger {
  color: #fff;
  background-color: #d44a26;
  background-image: none;
}
.alert-danger a {
  color: #fff;
}
.alert-danger h1, .alert-danger .h1 {
  color: #eee;
}

#maincontainer .list-group {
  margin: 1rem 0 0;
  padding-bottom: 10px;
}
#maincontainer .list-group-item {
  border: none;
  display: list-item;
  padding: 0;
}

#li_select_server.list-group-item,
#li_change_password.list-group-item,
#li_select_mysql_collation.list-group-item,
#li_user_preferences.list-group-item,
#li_select_lang.list-group-item,
#li_select_theme.list-group-item {
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  padding-left: 20px;
}
#li_select_server.list-group-item:hover,
#li_change_password.list-group-item:hover,
#li_select_mysql_collation.list-group-item:hover,
#li_user_preferences.list-group-item:hover,
#li_select_lang.list-group-item:hover,
#li_select_theme.list-group-item:hover {
  background: #f6f6f6;
}

#li_select_theme::after {
  content: "Scheme: win";
  margin-left: 10px;
}

.modal-header {
  color: #eee;
  background-color: #377796;
}

@media print {
  .hide {
    display: none;
  }

  body,
table,
th,
td {
    color: #000;
    background-color: #fff;
    font-size: 12px;
  }

  a:link {
    color: #000;
    text-decoration: none;
  }

  img {
    border: 0;
  }

  table,
th,
td {
    border: 0.1em solid #000;
    background-color: #fff;
  }

  table {
    border-collapse: collapse;
    border-spacing: 0.2em;
  }

  thead {
    border-collapse: collapse;
    border-spacing: 0.2em;
    border: 0.1em solid #000;
    font-weight: 900;
  }

  th,
td {
    padding: 0.2em;
  }

  thead th {
    font-weight: bold;
    background-color: #e5e5e5;
    border: 0.1em solid #000;
  }

  th.column_heading, th.column_action {
    border: 0.1em solid #000;
  }

  table tr.odd, table tr.even {
    border-left: 0.1em solid #000;
  }
  table tr.odd th, table tr.even th {
    border-bottom: 0.1em solid #000;
  }
  table.data th {
    border-bottom: 0.1em solid #000;
  }

  #page_content {
    position: absolute;
    left: 0;
    top: 0;
    width: 95%;
    float: none;
  }

  .sqlOuter {
    color: black;
  }

  .cDrop,
.cEdit,
.cList,
.cCpy,
.cPointer {
    display: none;
  }

  table tbody:first-of-type tr:nth-child(odd) {
    background: #fff;
  }
  table tbody:first-of-type tr:nth-child(odd) th {
    background: #fff;
  }
  table tbody:first-of-type tr:nth-child(even) {
    background: #dfdfdf;
  }
  table tbody:first-of-type tr:nth-child(even) th {
    background: #dfdfdf;
  }

  .column_attribute {
    font-size: 100%;
  }
}